import React, { useContext } from 'react';
import '../styles/Help.css';
import { PlataformContext } from '../Context/PlataformContext';
import Steps from '../Enums/Steps';

interface HelpProps {
  previusStep: Steps,
}

const Help: React.FC<HelpProps> = ({previusStep}) => {
    const {setStep} = useContext(PlataformContext);
    
    const handleBack = () => {
      setStep(previusStep);
    }

    if(previusStep === Steps.AskDimensions){
      return (
        <div>
          <span>Ajuda</span>
          <p>Nesta etapa voc deve escolher as dimenses do produto que deseja validar as medidas.</p>
          <br/>
          <ul>
            <li>C: Comprimento; Produtos que apenas uma dimenso  considerada. E.g: Linha, barra, cabo.</li>
            <li>CxL: Comprimento e Largura; Produtos que duas dimenses so consideradas. E.g: Toalha, cobertor.</li>
            <li>CxLxA: Comprimento, Largura e Altura; Produtos que trs dimenses so consideradas. E.g: Caixa.</li>
          </ul>
          <button className="backButton" onClick={handleBack}>Voltar</button>
        </div>
      );
    } else if (previusStep === Steps.AskNominal){
      return (
        <div>
          <span>Ajuda</span>
          <p>Nesta etapa voc deve informar a(s) medidas nominais do produto, isto , a medida informada para o cliente. Note que a unidade de medida (metro, centmetro, milmetro) no precisa ser especificada, apenas garanta que voc est utilizando a mesma unidade em todos os campos de medida, neste, e em todos os outros passos.</p>
          <button className="backButton" onClick={handleBack}>Voltar</button>
        </div>
      );
    } else if (previusStep === Steps.AskAdditionalData){
      return (
        <div>
          <span>Ajuda</span>
          <p>Nesta etapa voc deve informar a impreciso do equipamento de medio utilizado, em instrumentos analgicos como uma rgua ou fita mtrica a impreciso  metade da menor unidade de medida marcada no equipamento. Para os exemplos citados, geralmente so os milmetros, portanto temos uma impreciso de 0.5 milmetros. J para instrumentos digitais, temos como impreciso a menor unidade mostrada no equipamento, exemplo: Se uma balana de preciso mostra a medio 1.72g, a menor unidade mostrada  o centsimo de grama (0.01g), caso tenha o manual em mos  possivel encontrar nele tambm. Cuidado com a unidade de medida, informe a impreciso na mesma unidade que informou as medidas nominais.</p>
          <p>Nesta etapa voc deve tambm informar o tamanho do lote do produto avaliado, isto  necessrio para saber o tamanho da amostra, portanto, quantidade de medidas por dimenso, que deve ser analisada. Note que estas faixas so definidas pela portaria, no sendo possvel altera-las.</p>
          <button className="backButton" onClick={handleBack}>Voltar</button>
        </div>
      );
    } else{
      return (
        <div>
          <span>Ajuda</span>
          <p>Nesta etapa voc deve informar as medies obtidas na amostra separadas por virgulas (utilize ponto para informar decimais). Voc pode fazer isto tanto atravs de um arquivo .csv, ou digitando diretamente na caixa de texto. A estrutura do arquivo ou do texto puro so as mesmas, as medidas so separadas por virgula, e cada dimenso fica em uma linha.</p>
          <hr/>
          <p>Exemplos:</p>
          <hr/>
          <p>Linha (Apenas comprimento importa) com um lote de 10 unidades:</p>
          <p>4.98,5,5.01,5.02,4.99</p>
          <p>Temos as 5 medidas de comprimento necessrias definidas em uma linha</p>
          <hr/>
          <p>Toalha (Comprimento e largura importam) com um lote de 10 unidades:</p>
          <p>4.98,5,5.01,5.02,4.99</p>
          <p>3,2.99,3.02,2.97,3</p>
          <p>Temos as 5 medidas de comprimento definidas na primeira linha, e as 5 medidas de largura na segunda, totalizando 10 medidas necessrias.</p>
          <hr/>
          <p>Caixa (Comprimento, largura e altura importam) com um lote de 10 unidades:</p>
          <p>4.98,5,5.01,5.02,4.99</p>
          <p>3,2.99,3.02,2.97,3</p>
          <p>0.99,1,1.01,1.02,0.97</p>
          <hr/>
          <p>Temos as 5 medidas de comprimento definidas na primeira linha, e as 5 medidas de largura na segunda, e as 5 de altura definidas na terceira linha as totalizando 15 medidasm necessrias.</p>
          <p>Note que por consideramos lotes de 10 unidades apenas 5 medidas por dimenso foram necessrias, mas mais medidas so necessrias conforme maior o lote, esta relao  definida na portaria.</p>
          <p>Antes de finalizar voc deve validar os dados. Caso a caixa de texto fique com o contorno verde, est tudo certo e podemos finalizar, caso contrrio devemos ajustar algo nos dados.</p>
          <button className="backButton" onClick={handleBack}>Voltar</button>
        </div>
      );
    }
};

export default Help;
